import React, { Component, PropTypes } from 'react'

import Video from '../Video'
import IntroCard from '../IntroCard'
import Timeline from '../Timeline'
import TuitionFree from '../TuitionFree'

import './about.scss'

export default class About extends Component {
	render() {
		return (
			<div className="about__content">
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<p className="gray-text gray-text_bold">To provide China’s finest young musicians access to superior performance and training opportunities and to allow them to serve as ambassadors for classical music and their country on a world stage.</p>
					<p className="gray-text">
						The first annual National Youth Orchestra of China is proud to present an international concert tour of the United States and China during the summer of 2017, inaugurated by a gala performance in Carnegie Hall. For two weeks in July, approximately one hundred high school-aged musicians will gather on a university campus in the Northeast United States to participate in an intensive two-week training program under the direction of a world-class teaching faculty and a renowned guest conductor, Ludovic Morlot. After two weeks of preparation, the orchestra will debut in New York City and thereafter embark on a three-city concert tour of China featuring a soloist who will resonate with Chinese and Western audiences alike.
						<br/><br/>
						Inspired by Carnegie Hall’s own National Youth Orchestra of the United States of America (NYO-USA), NYO-China is meant to provide Chinese musicians access to the world-class education and performance model developed by its American counterpart. Members will have the chance to train under some of the finest living classical musicians and then perform as an orchestra on a world stage. After applying through a competitive virtual audition process, accepted members will be given the chance to serve as ambassadors for both classical music and their country. Moreover, to ensure that no superior musician will ever be turned away for lack of funds, the entire program – including housing, tuition, and travel – will be free of charge.
					</p>
					{/*
					<div className="wrapper-padding-top">
						<Video title="The Imperial March by the Vienna Philharmonic Orchestra">
							<source src="video/vienna.mp4" type="video/mp4"/>
						</Video>
					</div>
					*/}
				</div>
				<hr/>		 	
				<div className="wrapper-1000px">
					<div className="wrapper-padding-top wrapper-padding-bottom">
						{
							this.props.aboutData.map((item, index) => {
								return (
									<IntroCard
										key={index}
										image={item.image}
										title={item.title}
										description={item.description}
										align={item.align}/>
								)
							})
						}
					</div>
					<hr className="short"/>
					<Timeline
						auditionProcessData={this.props.auditionProcessData}
						concertTourData={this.props.concertTourData}/>
				</div>
				<hr/>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<TuitionFree/>
				</div>
			</div>
		)
	}
}

About.propTypes = {
	auditionProcessData: PropTypes.array.isRequired,
	concertTourData: PropTypes.array.isRequired,
	aboutData: PropTypes.array.isRequired
}


