import React from 'react'
import ReactDOM from 'react-dom'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import BigScreen from 'bigscreen'
import cx from 'classnames'

import './video.scss'

export default React.createClass({
	getInitialState: function() {
		return {
			isPlay: false,
			isFullscreen: false,
			isMuted: false,
			currentTime: 0,
			isBuffering: false,
			bufferedEnd: 0,
			duration: 0,
			volume: 0.5,
			isVolumeSliderVisible: false,
			isVolumeInDrag: false,
			playPauseAnimationActive: false
		}
	},
	handlePlay: function() {
		this.setState({isPlay: !this.state.isPlay})
	},
	handleVideoClick: function() {
		// first remove current animated element
		this.setState({
			playPauseAnimationActive: false
		}, function() {
			// then add a new one
			this.setState({
				playPauseAnimationActive: true,
				isPlay: !this.state.isPlay
			}, function() {
				// remove element after delay
				setTimeout(() => {
					this.setState({
						playPauseAnimationActive: false
					});
				}, 500)
			})
		})
	},
	handleMute: function() {
		this.setState({
			isMuted: !this.state.isMuted,
			volume: this.state.isMuted && this.state.volume === 0 ? 0.1 : this.state.volume
		})
	},
	handleFullscreen: function() {
		BigScreen.toggle(this.refs.container, () => {
			this.setState({isFullscreen: true})
		}, () => {
			this.setState({isFullscreen: false})
		}, () => {
			throw new Error('Error while enter fullscreen')
		});
	},

	handleVolumeMouseEnter: function() {
		this.setState({isVolumeSliderVisible: true})
	},
	handleVolumeMouseLeave: function() {
		this.setState({isVolumeSliderVisible: false})
	},
	handleVolumeSliderDragStart: function() {
		this.setState({isVolumeInDrag: true})
	},
	handleVolumeSliderDragEnd: function() {
		this.setState({isVolumeInDrag: false})
	},

	componentDidMount: function() {
		if ("ontouchstart" in document.documentElement) {
			return
		}

		let video = this.refs.video;

		this.enableBufferingDetection()
		//

		// wait whilte video loaded
		// then save duration to state
		let intervalID = setInterval(() => {
			if (video && video.readyState > 0) {
				this.setState({duration: video.duration})
				clearInterval(intervalID);
			}
		}, 200);

		// fired when video started to play
		let onVideoPlay = () => {
			video.currentTime = this.state.currentTime;
		}

		// fired every 0.03s
		let onVideoTimeUpdate = () => {
			if (!video.paused) {
				this.setState({
					currentTime: video.currentTime
				})
			}
		}

		// fired on the end of video
		let onVideoEnded = () => {
			this.setState({isPlay: false, currentTime: 0})
		}

		document.addEventListener('keypress', (event) => {
			let nodeName = (event.target.nodeName || event.target.tagName).toLowerCase()
			if (nodeName !== 'input' && nodeName !== 'textarea') {
			    if (event.which == 32) {
			    	event.preventDefault();
			        this.handleVideoClick()
			    }
			}
		});

		video.addEventListener('play', onVideoPlay);
		video.addEventListener('ended', onVideoEnded);

		setInterval(onVideoTimeUpdate, 30);
	},
	componentDidUpdate: function() {

		let video = this.refs.video;

		if (this.state.isPlay && video.paused) {
			video.play()
		} else if (!this.state.isPlay && !video.paused) {
			video.pause()	
		}

		video.volume = this.state.volume;
		video.muted = this.state.isMuted;
	},
	enableBufferingDetection() {
		let checkInterval  = 300.0, // check for is video stopping every 300ms
			lastPlayPos    = 0,
			currentPlayPos = 0,
			bufferingDetected = false,
			video = this.refs.video;

		let getBufferingEnd = (videoElement) => {
			return videoElement && videoElement.buffered.length ? videoElement.buffered.end(videoElement.buffered.length - 1) : 0.0
		}	

		let checkBuffering = () => {
			if (!video) return;

			let bufferedEnd = getBufferingEnd(video);
			if (this.state.bufferedEnd !== bufferedEnd) {
				this.setState({bufferedEnd})
			}

			if (video.duration == bufferedEnd) return;

		    currentPlayPos = video.currentTime

		    // checking offset, e.g. 1 / 50ms = 0.02
		    var offset = 1 / checkInterval

		    // if no buffering is currently detected,
		    // and the position does not seem to increase
		    // and the video isn't manually paused...
		    if (
		            !bufferingDetected 
		            && currentPlayPos < (lastPlayPos + offset)
		            && !video.paused
		        ) {
		    	this.setState({isBuffering: true})
		        bufferingDetected = true
		    }

		    // if we were buffering but the video has advanced,
		    // then there is no buffering
		    if (
		        bufferingDetected 
		        && currentPlayPos > (lastPlayPos + offset)
		        && !video.paused
		        ) {
		    	this.setState({isBuffering: false})
		        bufferingDetected = false
		    }
		    lastPlayPos = currentPlayPos
		}

		setInterval(checkBuffering, checkInterval)
	},
	handleVolumeLevelChange: function(value) {
		let newVolume = value / 100;
		this.setState({
			volume: newVolume,
			isMuted: this.state.isMuted && newVolume !== 0 ? false : this.state.isMuted, 
		}, function() {
			this.refs.video.volume = newVolume
		})
	},
	handleTimeChange: function(value) {
		let newTime = this.state.duration * (value / 100)
		this.setState({currentTime: newTime}, function() {
			this.refs.video.currentTime = newTime
		})
	},
	formatSeconds: function(seconds) {
		var hours = parseInt( seconds / 3600 ) % 24,
			minutes = parseInt( seconds / 60 ) % 60,
			seconds = parseInt(seconds % 60)

		return (hours + ":" + (minutes < 10 ? "0" + minutes : minutes) + ":" + (seconds  < 10 ? "0" + seconds : seconds));
	},
	render: function() {
		if ("ontouchstart" in document.documentElement) {
			return (
				<div className="video-mobile">
					<video controls>
				  		{this.props.children}
				  		Your browser does not support the video tag.
					</video>
				</div>
			)
		}

		var videoBlockClasses = cx('video', {'video_fullscreen': this.state.isFullscreen}),
			playButtonClasses = cx('video__play', {'video__play_pause': this.state.isPlay}),
			volumeContainerClasses = cx('video__volume-container', {'video__volume-container_control-visible': this.state.isVolumeSliderVisible || this.state.isVolumeInDrag}),
			volumeButtonClasses = cx('video__volume', {'video__volume_muted': this.state.isMuted || this.state.volume === 0})

		var percentsTime = (this.state.currentTime / this.state.duration) * 100;
		var percentsBuffered = (this.state.bufferedEnd / this.state.duration) * 100;
		var percentsVolume = (this.state.isMuted ? 0 : this.state.volume) * 100;

		var initialPlayButton,
			playPauseAnimatedCircle,
			bufferingBlock;

		if (this.state.playPauseAnimationActive) {
			var playPauseAnimatedCircleClasses = 'video__middle-play-pause ' + (this.state.isPlay ? 'video__middle-play-pause_play' : 'video__middle-play-pause_pause')
			playPauseAnimatedCircle = <ReactCSSTransitionGroup transitionName="video__middle-play-pause-animation" transitionAppear={true} transitionAppearTimeout={500} transitionEnterTimeout={500} transitionLeaveTimeout={500}>
				<div className={playPauseAnimatedCircleClasses}></div>
			</ReactCSSTransitionGroup>
		}

		if (!this.state.isPlay && this.state.currentTime === 0) {
			initialPlayButton = <div className="video__initial-play-button" onClick={this.handlePlay}></div>
		}
		
		if (!this.state.isMuted && this.state.volume > 0) {
			var volumeLevelClassName;
			if (percentsVolume <= 33) {
				volumeLevelClassName = 'video__volume_low';
			} else if (percentsVolume <= 66) {
				volumeLevelClassName = 'video__volume_medium';
			} else if (percentsVolume <= 100) {
				volumeLevelClassName = 'video__volume_high';
			}
			volumeButtonClasses += (' ' + volumeLevelClassName);
		}

		var titleItem = this.state.isPlay ? null : <div className="video__title">{this.props.title}</div>

		var currentTimeTooltipTextFormatter = function(value) {
			return this.formatSeconds(this.state.duration * (value / 100));
		}

		if (this.state.isBuffering && this.state.isPlay) {
			bufferingBlock = <BufferingBlock percents={percentsTime}/>
		}


		return (
			<div className={videoBlockClasses} ref="container">
				{titleItem}
				<video ref="video" onClick={this.handleVideoClick} onDoubleClick={this.handleFullscreen} preload="metadata" poster={this.props.poster}>
				  	{this.props.children}
				  	Your browser does not support the video tag.
				</video>
				{initialPlayButton}
				{playPauseAnimatedCircle}
				{bufferingBlock}
				<div className={volumeContainerClasses} onMouseEnter={this.handleVolumeMouseEnter} onMouseLeave={this.handleVolumeMouseLeave}>
					<div className={volumeButtonClasses} onClick={this.handleMute}></div>
					<div className="video__volume-slider-container">
						<ValueSlider
							value={percentsVolume}
							loadedValue={100}
							onSlide={this.handleVolumeLevelChange}
							onDragStart={this.handleVolumeSliderDragStart}
							onDragEnd={this.handleVolumeSliderDragEnd}
							/>
					</div>
				</div>
				<div className="video__controls">
					<div className={playButtonClasses} onClick={this.handlePlay}></div>
					<div className="video__current-time">{this.formatSeconds(this.state.currentTime)}</div>
					<div className="video__track-slider-container">
						<ValueSlider
							value={percentsTime}
							loadedValue={percentsBuffered}
							onChange={this.handleTimeChange}
							tooltipFunction={currentTimeTooltipTextFormatter.bind(this)}
							/>
					</div>
					
					
					<div className="video__duration">{this.formatSeconds(this.state.duration)}</div>
					<div className="video__fullscreen" onClick={this.handleFullscreen}></div>
				</div>

			</div>
		)
	}
})

class BufferingBlock extends React.Component {
	render() {
		return (
			<div className="video-buffering">
				<div className="video-buffering__container">
					<div className="video-buffering__loader"></div>
				</div>
			</div>
		)
	}
}

var ValueSlider = React.createClass({
	getDefaultProps: function() {
		return {
			value: 0,
			loadedValue: 0
		}
	},
	componentWillReceiveProps: function(nextProps) {
		if (!this.state.inDrag) {
			this.setState({
				value: nextProps.value,
				loadedValue: nextProps.loadedValue
			})
		}
	},
	getInitialState: function() {
		return {
			value: 0,
			loadedValue: 0,
			inDrag: false,
			tooltipVisible: false,
			tooltipValue: 0,
			dragStartX: 0,
			dragStartY: 0,
			initLeft: 0,
			initTop: 0
		}
	},
	getPointPosition: function(event) {
		var track = ReactDOM.findDOMNode(this),
			trackRect = track.getBoundingClientRect(),
			trackOffsetTop = trackRect.top + document.body.scrollTop,
			trackOffsetLeft = trackRect.left + document.body.scrollLeft,
			trackX = event.pageX - trackOffsetLeft,
			trackY = event.pageY - trackOffsetTop,
			trackWidth = trackRect.width,
			trackHeight = trackRect.height,
			percents = trackX / trackWidth * 100,
			value = percents;

		if (value < 0) value = 0;
		if (value > 100) value = 100;
		
		return value;
	},
	handleTrackMouseDown: function(event) {
		var value = this.getPointPosition(event)

		var handleEvent = new Event('mousedown');
		handleEvent.pageX = event.pageX;
		handleEvent.pageY = event.pageY;

		this.setState({
    		value: value,
    		dragStartX: event.pageX,
    		dragStartY: event.pageY,
    		inDrag: true
    	}, function() {
    		this.refs.handle.dispatchEvent(handleEvent)
    		this.props.onSlide && this.props.onSlide(value)
    		this.props.onChange && this.props.onChange(value)
    	})
	},
	handleTrackMouseMove: function(event) {
		event.stopPropagation()

		var value = this.getPointPosition(event);
		this.setState({
    		tooltipValue: value
    	})
	},
	handleTrackMouseEnter: function(event) {
		event.stopPropagation()
		this.setState({tooltipVisible: true})
	},
	handleTrackMouseLeave: function(event) {
		event.stopPropagation()
		this.setState({tooltipVisible: false})
	},
	handleHandleMouseDown(event) {
		event.stopPropagation()

		var dragTarget = this.refs.handle;

    	this.setState({
    		inDrag: true,
    		dragStartX: event.pageX,
    		dragStartY: event.pageY,
			initTop: dragTarget.offsetTop + dragTarget.getBoundingClientRect().height / 2,
			initLeft: dragTarget.offsetLeft + dragTarget.getBoundingClientRect().width / 2
    	});

    	this.props.onDragStart && this.props.onDragStart();

    	document.addEventListener("mousemove", this.handleHandleDocumentMouseMove);
	  	document.addEventListener("mouseup", this.handleHandleDocumentMouseUp);
	},

	handleHandleDocumentMouseMove(event) {
		event.stopPropagation()

    	if (!this.state.inDrag) {
    		return;
    	}
    	var dragRect = this.refs.handle.parentNode.getBoundingClientRect(),
    		parentWidth = dragRect.width,
    		parentHeight = dragRect.height;

    	var top = (this.state.initTop + event.pageY - this.state.dragStartY),
    		left = (this.state.initLeft + event.pageX - this.state.dragStartX);


    	if (left < 0) left = 0;
    	if (left > parentWidth) left = parentWidth;

    	var value = left / parentWidth * 100;

    	this.props.onSlide && this.props.onSlide(value);
    	this.setState({
    		value: value,
    		tooltipValue: value
    	})
	},

	handleHandleDocumentMouseUp(event) {
  		event.stopPropagation()

  		this.props.onChange && this.state.inDrag && this.props.onChange(this.state.value)
  		this.setState({inDrag: false, initLeft: 0, initTop: 0});

  		this.props.onDragEnd && this.props.onDragEnd();

  		document.removeEventListener("mousemove", this.handleHandleDocumentMouseMove);
		document.removeEventListener("mouseup", this.handleHandleDocumentMouseUp);
	},

	componentDidMount: function() {
	  	this.refs.handle.addEventListener("mousedown", this.handleHandleMouseDown);
	},

	componentWillUnmount() {
		this.refs.handle.removeEventListener("mousedown", this.handleHandleMouseDown);
	},

	render: function() {
		var percents = this.state.value + '%',
			loadedPercents = this.state.loadedValue + '%',
			tooltip;

		if ((this.state.tooltipVisible || this.state.inDrag) && this.props.tooltipFunction) {
			let tooltipPercents = this.state.tooltipValue + '%'
			tooltip = <div className="value-slider-track__tooltip" style={{left: tooltipPercents}}>{this.props.tooltipFunction(this.state.tooltipValue)}</div>
		}

		return (
			<div className="value-slider-track value-slider-track_horizontal" onMouseDown={this.handleTrackMouseDown} onMouseMove={this.handleTrackMouseMove} onMouseEnter={this.handleTrackMouseEnter} onMouseLeave={this.handleTrackMouseLeave}>
				<div className="value-slider-track__loaded" ref="loaded" style={{width: loadedPercents}}></div>
				<div className="value-slider-track__done" ref="done" onMouseMove={this.handlePointMouseMove} style={{width: percents}}></div>
				<div className="value-slider-track__handle" ref="handle" style={{left: percents}}></div>
				{tooltip}
			</div>
		)
	}
})


