require("babel-polyfill");

import React from 'react'
import {render} from 'react-dom'
import { Router, Route, Link, browserHistory, hashHistory } from 'react-router'

import 'whatwg-fetch'

import './common.scss'
import './icons.css'

import Landing from './routes/Landing'
import Lost from './routes/Lost'
import Newsroom from './routes/Newsroom'
import NewsroomItem from './routes/NewsroomItem'
import Privacy from './routes/Privacy'
import Legal from './routes/Legal'
import Leadership from './routes/Leadership'
import Sponsors from './routes/Sponsors'

// get data for landing, then load app
fetch('data.json')
	.then(response => response.json())
	.then(data => {

		const LandingWrapper = React.createClass({
			render() {
				return <Landing data={data}/>
			}
		})
		
		render(
			<Router onUpdate={() => window.scrollTo(0, 0)} history={browserHistory}>
			    <Route path="/" component={LandingWrapper}/>
			    <Route path="newsroom/:newsId" component={NewsroomItem}/>
			    <Route path="newsroom" component={Newsroom}/>
			    <Route path="privacy" component={Privacy}/>
			    <Route path="legal" component={Legal}/>
			    <Route path="leadership" component={Leadership}/>
			    <Route path="sponsors" component={Sponsors}/>
			    <Route path="*" component={Lost}/>
			</Router>,
			document.getElementById('app')
		)
	});



