import React from 'react'
import ArtistCard from '../ArtistCard'

/*
	props: array of artists
	@artists - array with objects like:
		{
			photo: string,
			title: string,
			subtitle: string,
			description: string
		}
*/
export default class Artists extends React.Component {
	render() {
		let artists = this.props.artists.map((artist, index) => {
			let align = index % 2 === 0 ? 'left' : 'right';
			return (
				<ArtistCard
					image={artist.photo}
					title={artist.title}
					subtitle={artist.subtitle}
					description={artist.description}
					align={align}
					key={index}
				/>
			)
		})

		return (
			<div>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">
					<div className="gray-text">Participation in NYO-China comes with the opportunity to perform with some of the most talented musicians in the world. This year, the orchestra will be led by guest conductor Ludovic Morlot and music director Chen Lin. In addition to representing the highest standard of classical performance art, these remarkable individuals have each demonstrated a passion for music education. In preparing the musicians of NYO-China and ultimately performing alongside them, there can be no better mentors than these extraordinary artists.</div>
				</div>
				<hr className="short"/>
				<div className="wrapper-1000px wrapper-padding-top wrapper-padding-bottom">{artists}</div>
			</div>
		)
	}
}


