import React from 'react'
import {TimelineItem, Tooltip} from './TimelineItem'

import './timeline-block.scss';

/*
	props: data for timeline
	@auditionProcessData
	@concertTourData
*/
export default class Timeline extends React.Component {
	state = {
		openedTooltipIndex: false
	}

	onItemIn = (index) => {
		if (!(document.body.offsetWidth <= 768)) {
			this.setState({openedTooltipIndex: index})
		}
	}

	onItemOut = () => {
		if (!(document.body.offsetWidth <= 768)) {
			this.setState({openedTooltipIndex: false})
		}
	}

	onItemClick = (index) => {
		if (document.body.offsetWidth <= 768) {
			this.setState({ openedTooltipIndex: this.state.openedTooltipIndex === index ? false : index })
		}
	}

	render() {
		return (
		  	<div className="timeline">
				<TimelineItem
					title="Audition Process"
					date="October 15, 2016 - January 31, 2017"
					icon="audition-process"
					align="left"
					isTooltipOpen={this.state.openedTooltipIndex === 1}
					onItemIn={this.onItemIn.bind(this, 1)}
					onItemOut={this.onItemOut}
					onItemClick={this.onItemClick.bind(this, 1)}>

					<Tooltip align="left">
						<ul className="tooltip-list">{
							this.props.auditionProcessData.map((item, index) => {
								return (
									<li className="tooltip-list-item" key={index}>
						  				<div className="tooltip-list-item__title">{item.title}</div>
						  				<div className="tooltip-list-item__date">{item.date}</div>
						  			</li>
								)
							})
						}</ul>
			  		</Tooltip>

				</TimelineItem>

				<TimelineItem
					title="Training Residency"
					date="July 8, 2017 - July 21, 2017"
					icon="w-conservatory"></TimelineItem>

				<TimelineItem
					title="Concert Tour"
					date="July 22, 2017 - July 30, 2017"
					icon="concert-tour"
					align="right"
					isTooltipOpen={this.state.openedTooltipIndex === 2}
					onItemIn={this.onItemIn.bind(this, 2)}
					onItemOut={this.onItemOut}
					onItemClick={this.onItemClick.bind(this, 2)}>

				  	<Tooltip align="right">
						<ul className="tooltip-list" ref="asd">
						{
							this.props.concertTourData.map((item, index) => {
								return (
									<li className="tooltip-list-item" key={index}>
						  				<div className="tooltip-list-item__title">{item.city}, {item.country}</div>
										<div className="tooltip-list-item__date">{item.date}</div>
						  			</li>
								)
							})
						}
						</ul>
				  	</Tooltip>

				</TimelineItem>
		  	</div>
		)
	}
}


