import React from 'react'
import ReactDOM from 'react-dom';

import classNames from 'classnames'

import Navigation from '../../components/Navigation'
import Hero from '../../components/Hero'
import IntroSliders from '../../components/IntroSliders'
import ImageTitle from '../../components/ImageTitle'
import About from '../../components/About/'
import Artists from '../../components/Artists/'
import TrainingResidency from '../../components/TrainingResidency/'
import ConcertTour from '../../components/ConcertTour/'
import Auditions from '../../components/Auditions/'
import Contact from '../../components/Contact'
import Footer from '../../components/Footer'

export default class extends React.Component {
  	render() {
	  	// from data.json
		var data = this.props.data;
	    return (
	    	<div className="app">
	    		<Navigation />
	    		<Hero/>

				<IntroSliders
					endorsements={data.endorsements}
					sponsors={data.sponsors}/>

				<ImageTitle image="img/about.jpg">About</ImageTitle>
				<About
					aboutData={data.about_data}
					auditionProcessData={data.audition_process_data}
					concertTourData={data.concert_tour_data}/>

				<ImageTitle image="img/artists.jpg">Artists</ImageTitle>
				<Artists artists={data.headlining_artists}/>

				<ImageTitle image="img/training-residency.jpg">Training Residency</ImageTitle>
				<TrainingResidency
					images={data.w_conservatory.image_grid}
					faculty={data.w_conservatory.faculty}
					auditions={data.auditions}/>

				<ImageTitle image="img/concert-tours.jpg">Concert Tour</ImageTitle>
				<ConcertTour data={data.concert_tour_data}/>

				<ImageTitle image="img/auditions.jpg">Auditions</ImageTitle>
				<Auditions auditionsRequirements={data.auditions}/>

				<Contact/>

				<Footer/>
			</div>
	    )
  	}
}


