import React, { Component, PropTypes } from 'react'
import smoothScroll from 'smooth-scroll'

import InstrumentsBlock from '../InstrumentsBlock'
import Instrument from '../Instrument'
import { TimelineItem, Tooltip } from '../Timeline/TimelineItem'
import { BigIcons, BigIcon } from '../BigIcons'

import './auditions.scss'

const SCROLLING_OPTIONS = {
	speed: 300,
	easing: 'easeInOutQuad',
	updateURL: false
}

export default class Auditions extends Component {

	state = {
		categoryIndex: -1,
		instrumentIndex: -1,
	}

	shouldComponentUpdate(nextProps, nextState) {
		return this.categoryIndex !== nextState.categoryIndex && this.instrumentIndex !== nextState.instrumentIndex;
	}

	isCategorySelected() {
		return this.state.categoryIndex !== -1
	}

	isInstrumentSelected() {
		return this.state.instrumentIndex !== -1
	}

	scrollTo(selector) {
		smoothScroll.animateScroll(selector, null, SCROLLING_OPTIONS);
	}

	handleCategoryClick(index) {
		this.setState({
			categoryIndex: this.state.categoryIndex === index ? -1 : index,
			instrumentIndex: -1
		}, () => {
			if (this.isCategorySelected()) {
				this.scrollTo('#auditions-categories');
			}
		})
	}

	handleInstrumentClick(index) {
		this.setState({
			instrumentIndex: this.state.instrumentIndex === index ? -1 : index,
		}, () => {
			if (this.isInstrumentSelected()) {
				this.scrollTo('#auditions-instruments');
			}
		})
	}

	render() {
		let { categoryIndex, instrumentIndex } = this.state,
			{ auditionsRequirements } = this.props,
			instrumentsBlock,
			requirementsBlock;

		// list of categories
		let categories = auditionsRequirements.map((category, index) => {
			return <Instrument
						active={categoryIndex === index}
						title={category.category}
						icon={category.icon}
						key={index}
						onClick={this.handleCategoryClick.bind(this, index)}/>
		})

		// list of instruments in category
		if (this.isCategorySelected() && !(auditionsRequirements[categoryIndex].requirements instanceof Array)) {
			instrumentsBlock = (
				<InstrumentsBlock gray id="auditions-instruments">
				{
					auditionsRequirements[categoryIndex].instruments.map((instrument, index) => {
						return <Instrument
									inversed
									active={instrumentIndex === index}
									title={instrument.name}
									icon={instrument.icon}
									key={index}
									onClick={this.handleInstrumentClick.bind(this, index)}
								/>
					})
				}
				</InstrumentsBlock>
			);
		}

		// list of requirements of instrument
		if (this.isInstrumentSelected() || (this.isCategorySelected() && auditionsRequirements[categoryIndex].requirements instanceof Array)) {
			let requirementsData = (auditionsRequirements[categoryIndex].requirements) || auditionsRequirements[categoryIndex].instruments[instrumentIndex].requirements;
			let filesData = (auditionsRequirements[categoryIndex].files) || auditionsRequirements[categoryIndex].instruments[instrumentIndex].files;

			requirementsBlock = (
				<div className="requirements">
					<div className="requirements-block">
						<ul className="requirements-list">{
							requirementsData.map((requirement, index) => {
								return (
									<li className="requirements-list-item" key={index}>
										<span className="requirements-list-item__note"><i className="icon icon-note"></i></span>
										{requirement}
									</li>
								)
							})
						}</ul>
					</div>
					<hr className="short"/>
					<BigIcons>
						<BigIcon icon="uncut" grayTitle>Video submissions<br/>should be uncut</BigIcon>
						<BigIcon icon="unedited" grayTitle>Video submissions<br/>should be unedited</BigIcon>
						<BigIcon icon="face" grayTitle>Video submissions<br/>shoud show your face</BigIcon>
					</BigIcons>
					<hr className="short"/>
					<div className="files">
						<ul className="files-list">{
							filesData.map(file => <li className="files-list__item" key={file.id}><a href={file.file}>{file.name}</a></li>)
						}</ul>
					</div>
				</div>
			)
		}

		return (
			<div className="auditions">
				<div className="wrapper-1000px wrapper-padding-top">
					<p className="gray-text">Auditions will be virtual, with all candidates submitting their application materials through an online portal accessible through this website. Specific audition requirements vary by instrument, but all applicants must provide a résumé, a written biographical essay, a letter of recommendation from a relevant music instructor, and video clips of various requested performances. All students from mainland China who will be between the ages of 14 and 18 during the course of the program will be eligible to apply. The application portal will open on October 15th, 2016 and close on January 31st, 2017.</p>
					<div className="timeline timeline_auditions">
						{/* TODO: Why isnt' this taking the data from "audition_process_data" of data.json? */}
						<TimelineItem title="Applications Open" date="October 15, 2016" icon="clock"/>
						<TimelineItem title="Applications Due" date="January 31, 2017" icon="applications-due"/>
						<TimelineItem title="Judging Period" date="January 31, 2017 - February 15, 2017" icon="judging"/>
						<TimelineItem title="Decisions Released" date="February 15, 2017" icon="decisions-release"/>
						<TimelineItem title="Matriculations Confirmed" date="March 1, 2017" icon="confirmation-due"/>
				  	</div>
				</div>
				<hr/>
				<div className="wrapper-1000px wrapper-padding-top">
					<h2 className="middle-title">General Requirements</h2>
					<BigIcons full>
						<BigIcon icon="resume" grayTitle>Resume</BigIcon>
						<BigIcon icon="recommendation" grayTitle>Letters of Recommendation</BigIcon>
						<BigIcon icon="video-submission" grayTitle>Video Submissions</BigIcon>
						<BigIcon icon="payment" grayTitle>Payment of RMB 250</BigIcon>
					</BigIcons>
				</div>
				<hr/>
				<div className="wrapper-1000px wrapper-padding-top">
					<h2 className="middle-title">Instrument Specific Requirements</h2>
				</div>
				<InstrumentsBlock id="auditions-categories">{categories}</InstrumentsBlock>
				{instrumentsBlock}
				{requirementsBlock}
				<button className="button button_apply fade-tooltip disabled" data-title="Applications Not Yet Open">Apply</button>
			</div>
		)
	}
}

Auditions.propTypes = {
	auditionsRequirements: PropTypes.array.isRequired
}


